import axios from 'axios';
import { BaseUrl } from '../shared/BaseUrl';
import { myHeaders } from '../shared/MyHeaders';



export async function getAllWalletHistory(page) {
    try {
        const response = await axios.get(`${BaseUrl}/wallethistories?page=${page}&size=5`
            , { headers: myHeaders })
        return response;
    } catch (error) {
        console.log(error);
    }
}

export async function deleteDataWalletHistory(historyId) {
    try {
        const response = await axios.delete(`${BaseUrl}/wallethistory/${historyId}`
            , { headers: myHeaders })
        return response;
    } catch (error) {
        console.log(error);
    }
}