import React, { Component } from 'react';
import {
  Button,
  Spinner,
  CardBody,
  CardHeader,
  Table,
  ModalHeader, ModalBody, ModalFooter, Modal
} from 'reactstrap';
import { DETAIL_DATA, FETCH_DATA, FETCH_COMPLETE, DELETE_DATA, SET_TOTAL_PAGES } from "../../reducer/Action";
import { connect } from 'react-redux'
import ModalWallet from "../../component/ModalWallet";
import { getAllWalletHistory, deleteDataWalletHistory } from '../../services/WalletsService';
import PageComponent from '../../component/PageComponent';


class WalletHistoryTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
    }
  }

  componentDidMount() {
    this.getData(0);
  }

  getData = (page) => {
    const { fetchData, fetchComplete, setTotalPages } = this.props;
    fetchData();
    getAllWalletHistory(page).then((response) => {
      setTotalPages(response.data.totalPages)
      fetchComplete(response.data.content)
    })
      .catch(err => {
        console.log(err);
      })
  }

  handleDetail = (historyId) => {
    const { detailData } = this.props;
    this.toggleModal();
    detailData(historyId);
  }

  handleDelete = (historyId) => {
    const { deleteData } = this.props;
    deleteDataWalletHistory(historyId).then((response) => {
      console.log(response);
      deleteData(historyId);
    })
  }

  generateTableRows = () => {
    const { walletHistories, isLoading } = this.props;

    let rows = (
      <tr>
        <td colSpan="6" className="table-warning text-center"><strong><em>Data Still Empty</em></strong></td>
      </tr>
    );

    if (walletHistories.length > 0) {
      rows = <tr><td colSpan="6" className="table-warning text-center"><Spinner color={"danger"}></Spinner></td></tr>
      if (!isLoading) {
        rows = walletHistories.map((history, index) => {
          return (
            <tr key={index}>
              <td className="text-center">{index + 1}</td>
              <td className="text-center">{history.wallet.owner.username}</td>
              <td className="text-center">{history.amount}</td>
              <td className="text-center">{history.txrDate}</td>
              <td className="text-center">{history.type}</td>
              <td className="text-center"><Button color="danger" onClick={() => this.handleDelete(history.id)}>Delete</Button></td>
            </tr>
          )
        });
      };
    }
    return rows;
  }

  render() {
    const { form, totalPages } = this.props;

    return (
      <div>
        <CardHeader>
          <i className="fa fa-align-justify"></i> Data Wallet Histories
        </CardHeader>
        <CardBody>
          <Table hover bordered striped responsive size="sm">
            <thead>
              <tr>
                <th className="text-center">No.</th>
                <th className="text-center">Name</th>
                <th className="text-center">Amount</th>
                <th className="text-center">Date Transaction</th>
                <th className="text-center">Type Transaction</th>
                <th className="text-center" colSpan="2">Action</th>
              </tr>
            </thead>
            <tbody>
              {this.generateTableRows()}
            </tbody>
          </Table>
          <PageComponent totalPages={totalPages} getData={this.getData} />
        </CardBody>

        <Modal isOpen={this.state.modal} toggle={this.toggleModal} className={'modal-info' + this.props.className}>
          <ModalHeader toggle={this.toggleModal}><strong>Detail Wallet History</strong></ModalHeader>
          <ModalBody>
            <ModalWallet form={form} />
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleModal}>Cancel</Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }

}



function mapStateToProps(state) {
  return { ...state }
}

function mapDispatchToProps(dispatch) {
  return {
    detailData: (payload) => dispatch({ type: DETAIL_DATA, payload }),
    deleteData: (payload) => dispatch({ type: DELETE_DATA, payload }),
    fetchData: () => dispatch({ type: FETCH_DATA }),
    fetchComplete: (payload) => dispatch({ type: FETCH_COMPLETE, payload }),
    setTotalPages: (payload) => dispatch({ type: SET_TOTAL_PAGES, payload }),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(WalletHistoryTable);
